module.exports = "/* The switch - the box around the slider */\n.switch {\n    position: relative;\n    display: inline-block;\n    width: 60px;\n    height: 34px;\n  }\n/* Hide default HTML checkbox */\n.switch input {\n    opacity: 0;\n    width: 0;\n    height: 0;\n  }\n/* The slider */\n.slider {\n    position: absolute;\n    cursor: pointer;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: rgb(252, 0, 0);\n    transition: .4s;\n  }\n.slider:before {\n    position: absolute;\n    content: \"\";\n    height: 26px;\n    width: 26px;\n    left: 4px;\n    bottom: 4px;\n    background-color: white;\n    transition: .4s;\n  }\ninput:checked + .slider {\n      \n    background-color: rgb(82, 243, 33);\n  }\ninput:focus + .slider {\n    box-shadow: 0 0 1px rgb(82, 243, 33);\n  }\ninput:checked + .slider:before {\n    -webkit-transform: translateX(26px);\n    transform: translateX(26px);\n  }\n/* Rounded sliders */\n.slider.round {\n    border-radius: 34px;\n  }\n.slider.round:before {\n    border-radius: 50%;\n    \n  }\n/*# sourceMappingURL=data:application/json;base64, */"